import * as React from 'react';

import { NavBar, Icon, List, ListView, Toast, SwipeAction, Modal, WhiteSpace} from "antd-mobile";
import { History, Location } from "history";
import { UserService } from '../../service/UserService';
import { model } from '../../model/model';
import { UserStorage } from "../../storage/UserStorage";
import { UIUtil } from 'src/utils/UIUtil';

import de_bank from "../../assets/de_bank.png"
import den_bank from "../../assets/den_bank.png"


interface BankCardProps {
    history: History,
    location: Location
}

interface BankCardState {
    height: number,
    isLoading: boolean,
    dataSource: any,
    num: number,
}

export class BankCarde extends React.Component<BankCardProps, BankCardState> {
    lv:any
    constructor(props: BankCardProps) {
        super(props)
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1: model.TransactionItem, row2: model.TransactionItem) => row1 !== row2,
          });
        this.state = {
            height:  document.documentElement.clientHeight - 200,
            dataSource,
            isLoading: true,
            num: 0
        }
    }
    onRedirectBack = () => {
        const history = this.props.history
        this.state.num!=0?history.goBack():history.push("/homee");
    }
    onEndReached = (event:any) => {
        if (this.state.isLoading) {
          return;
        }
    }
    getBankCard = () => {
        UserService.Instance.listPayment().then( (res:any) => {
            this.setState({
                dataSource: this.state.dataSource.cloneWithRows(res.data),
                isLoading: false,
                num:res.data.length
            })
            if(res.data.length == 0){
                UIUtil.showInfo("请添加银行卡");
                return;
            }
            let list = res.data,isDefault = true;
            list.map((data:any)=>{
                if(data.type == 2){
                    isDefault = false
                }
            })
            if(isDefault){
                this.setDefault(res.data[0].name,res.data[0].bank_name,res.data[0].account);
            }
        }).catch( err => {
            const message = (err as Error).message
            Toast.fail(message)
        })
    }
    setDefault = (name:string,bank_name:string,bank_card_number:string) => {
        UserService.Instance.defaultPayment(bank_card_number).then( () => {
            Modal.alert('设置默认卡成功', '卡号：'+bank_card_number);
            UserStorage.setCookie('bankcard',bank_card_number)
            let bank:any = {name:name,bank_name:bank_name,bank_card_number:bank_card_number};
            bank = JSON.stringify(bank)
            UserStorage.setCookie('bank',bank)
            this.getBankCard();
        }).catch( err => {
            const message = (err as Error).message
            Toast.fail(message)
        })
    }
    public componentDidMount () {
        this.getBankCard()
    }
    public render() {
        const separator = (sectionID: number, rowID: number) => (
            <div
              key={`${sectionID}-${rowID}`}
              style={{
                backgroundColor: '#F5F5F5',
                height: 1,
              }}
            />
          );
  
          const row = (rowData: model.bankCardItem, sectionID: number, rowID: number) => {
     
            return (
                <SwipeAction
                    style={{ backgroundColor: 'gray' }}
                    autoClose
                    right={[
                        {
                        text: 'delete',
                        onPress: () => {
                            UserService.Instance.deletePayment(rowData.account).then( (res:any) => {
                                Modal.alert('prompt', 'successfully deleted');
                                if(rowData.type == "2"){
                                    UserStorage.delCookie('bankcard');
                                }
                                UserStorage.setCookie('bank','')
                                this.getBankCard();
                            }).catch( err => {
                                const message = (err as Error).message
                                Toast.fail(message)
                            })
                        },
                        style: { backgroundColor: '#F4333C', color: 'white' },
                        },
                    ]}
                    >
                    <List.Item className={rowData.type=='1'?'':'ddress-footer-button-container'} 
                        thumb={rowData.type=='1'?<div className="icon_bank"><img src={den_bank} alt=""/></div>:<div className="icon_bank"><img src={de_bank}></img></div>}
                        onClick={e => {
                            if('2' == rowData.type){
                                return;
                            }
                            this.setDefault(rowData.name,rowData.bank_name,rowData.account)
                        }}>
                        {rowData.name}<List.Item.Brief>{rowData.bank_name}    BankCard number：{rowData.account}</List.Item.Brief>
                    </List.Item>
                </SwipeAction>
            );
          };
        return (
            <div className="">
                <NavBar icon={<Icon type="left" />} 
                    onLeftClick={ this.onRedirectBack}
                    rightContent={[
                        <div key="1" className="bankcard-navbar-right" onClick={()=>this.props.history.push("/bankCardAdde")}>+</div>
                    ]}
                    className="home-navbar" >
                        <div className="nav-title">BankCard</div>
                </NavBar>
                <WhiteSpace size="lg"/>
                <div className="fans-list-view-container">
                    <ListView 
                            ref={el => this.lv = el}
                            dataSource={this.state.dataSource}
                            renderFooter={() => (<div style={{ padding: 30, textAlign: 'center' }}>
                            {this.state.isLoading ? 'Loading...' : ''}
                            </div>)}
                            renderRow={row}
                            renderSeparator={separator}
                            className="am-list"
                            pageSize={4}
                            // useBodyScroll
                            onScroll={() => { console.log('scroll'); }}
                            scrollRenderAheadDistance={500}
                            onEndReached={this.onEndReached}
                            onEndReachedThreshold={10}
                            style={{
                                height: this.state.height,
                                overflow: 'auto',
                            }}
                        />
                </div>
            </div>
        )
    }
}


