import * as React from 'react';

import { NavBar, Icon, List, InputItem, Button, WhiteSpace, Toast, Modal} from "antd-mobile";
import { History, Location } from "history";
import { UserService } from '../../service/UserService';
import { UserStorage } from "../../storage/UserStorage";
import { Redirect } from "react-router-dom";

interface BankCardAddProps {
    history: History,
    location: Location
}

interface BankCardAddState {
    redirectToReferrer: boolean
}

export class BankCardAdd extends React.Component<BankCardAddProps, BankCardAddState> {
    name:string
    bankname:string
    bankId: string
    pages: string

    constructor(props: BankCardAddProps) {
        super(props)
        // this.pages = "/bankCard";
        this.state = {
            redirectToReferrer: false
        }
    }
    onRedirectBack = () => {
        const history = this.props.history
        history.goBack();
    }
    onNameBlur = (value: string) => {
        this.name = value
    }

    onBankNameBlur = (value: string) => {
        this.bankname = value
    }

    onBankid = (value: string) => {
        this.bankId = value
    }

    onSubmit = () => {
        const nameInfo = "请输银行卡户名"
        const banknameInfo = "请输入银行名"
        const idInfo = "请输入银行卡号"
        if (!this.name) {
            Toast.info(nameInfo)
            return
        } 
        if (!this.bankname) {
            Toast.info(banknameInfo)
            return
        } 
        if (!this.bankId) {
            Toast.info(idInfo)
            return
        }
        UserService.Instance.addPayment(this.name,this.bankname, this.bankId).then( (res:any) => {
            const alert = Modal.alert
            alert('提示','新增成功')
            if(this.pages == "/walletQuiet"||this.pages == "/wallet"){
                UserService.Instance.defaultPayment(this.bankId).then( () => {
                    UserStorage.setCookie('bankcard',this.bankId);
                    this.setState({
                        ...this.state,
                        redirectToReferrer: true
                    })
                })
            }else{
                this.props.history.goBack();
            }
        }).catch( err => {
            const message = (err as Error).message
            Toast.fail(message)
        })
        
    }

    public componentDidMount (){
        // const page = this.props.location.state&&this.props.location.state.page||'';
        // if(page == 'wq'){
        //     this.pages = "/walletQuiet";
        // }
        // if(page == 'ww'){
        //     this.pages = "/wallet";
        // }
    }
    public render() {
        const {redirectToReferrer} = this.state;
        if (redirectToReferrer) {
            const to = {
                pathname: this.pages
            }
            return <Redirect to={to} />
        }
        return (
            <div className="">
                <NavBar icon={<Icon type="left" />} 
                    onLeftClick={ this.onRedirectBack}
                    className="home-navbar" >
                        <div className="nav-title">新增银行卡</div>
                </NavBar>
                <List renderHeader={() => ''} className="content-item-border">
                    <InputItem type="text" placeholder="请输入银行卡户名" onBlur={this.onNameBlur}>银行卡户名</InputItem>

                    <InputItem type="text" placeholder="请输入银行名" onBlur={this.onBankNameBlur}>银行名</InputItem>
                
                    <InputItem type="digit" placeholder="请输入银行卡号" onBlur={this.onBankid}>银行卡号</InputItem>
                
                </List>
                <WhiteSpace size="lg" />
                <WhiteSpace size="lg" />
                <div className="fans-footer">
                    <Button className="login-button" onClick={this.onSubmit}>提交新增</Button>
                </div>
            </div>
        )
    }
}


