import * as React from 'react';
import { NavBar, Icon, List, Button} from "antd-mobile";
import { Redirect } from "react-router-dom";
import { History } from "history";
import "./Settings.css"
import { UserStorage } from "../../storage/UserStorage";

import iconLogpwd from "../../assets/settint_logpwd.png"

interface SettingsProps {
    history: History,
    
}

interface SettingsState {
    redirectToLogin: boolean,
    checkeds: boolean
}

export class Settings extends React.Component<SettingsProps, SettingsState> {

    constructor(props: SettingsProps) {
        super(props)
        this.state = {
            redirectToLogin: false,
            checkeds: false
        }
    }

    onRedirectBack = () => {
        const history = this.props.history
        if(this.state.checkeds){
            history.push('/home')
        } else {
            history.push('/homee')
        }
    }
    
    onLogout = () => {
        UserStorage.delCookie('User.AccessTokenKey');
        UserStorage.delCookie('gesture');
        UserStorage.delCookie("touchpwd");
        this.setState( {
            redirectToLogin: true
        })
    }

    public render() {
        if (this.state.redirectToLogin) {
            if(this.state.checkeds){
                const to = {
                    pathname: "/login"
                }
                return <Redirect to={to} />
            } else {
                const to = {
                    pathname: "/logine"
                }
                return <Redirect to={to} />
            }
        }
        return (
            <div className="fans-container">
                <NavBar mode="light" icon={<Icon type="left" />} 
                    onLeftClick={ this.onRedirectBack}
                    className="home-navbar" >
                        <div className="nav-title">{this.state.checkeds?'设置':'Setting'}</div>
                </NavBar>
                <List renderHeader={() => ''} className="my-list">
                    <List.Item thumb={iconLogpwd} arrow="horizontal" onClick={()=>{
                        if(this.state.checkeds){
                            this.props.history.push("/update_pwd")
                        } else {
                            this.props.history.push("/update_pwde")
                        }
                    }}>{this.state.checkeds?'修改密码':'Modify the login password'}</List.Item>
                    <List.Item thumb={iconLogpwd} extra={this.state.checkeds?'中文':'English'} onClick={() => {
                        this.setState({
                            checkeds : !this.state.checkeds
                        })
                    }}>{this.state.checkeds?'语言切换':'Language'}</List.Item>
                </List>
                <div className="fans-footer">
                    <Button onClick={this.onLogout} >{this.state.checkeds?'退出登录':'Exit account'}</Button>
                </div>
            </div>
        )
    }
}


