import * as React from 'react';
import './App.css';
import { UserStorage } from "./storage/UserStorage";
// import { Button } from 'antd-mobile'
import { Switch, Route } from "react-router-dom";
import { PrivateRoute } from "./components/PrivateRoute";
import { Login } from "./pages/login/Login";
import { Register } from "./pages/register/Register";
import { IdCard } from "./pages/idcard/IdCard";
import { Home } from "./pages/home/Home";
import { Homee } from "./pagese/home/Home";
import { MyTeam } from "./pages/myTeam/MyTeam";
import { Settings } from "./pages/settings/Settings";
import { Sales } from "./pages/sales/Sales";
import { Message } from "./pages/message/Message";
import { Community } from "./pages/community/Community";
import { TransactionPwd } from "./pages/transactionPwd/TransactionPwd";
import { TransactionPwdForget } from "./pages/transactionPwd/TransactionPwdforget";
import { TouchPwd } from "./pages/touchPwd/TouchPwd";
import { UpdatePwd } from "./pages/updatePwd/UpdatePwd";
import { ForgetPwd } from "./pages/forgetPwd/ForgetPwd";
import { BankCardAdd } from "./pages/bankCard/BankCardAdd";
import { BankCard } from "./pages/bankCard/BankCard";
import { Feedback } from "./pages/feedback/Feedback";
import { IncomeRecord } from "./pages/IncomeRecord/IncomeRecord";
import { Exchange } from "./pages/exchange/Exchange";
import { ExchangeAgreement } from "./pages/exchangeAgreement/ExchangeAgreement";
import { Change } from "./pages/change/Change"
import { Wallet } from "./pages/wallet/Wallet";
import { WalletQuiet } from "./pages/wallet/WalletQuiet";
import { Wallete } from "./pagese/wallet/Wallet";
import { WalletQuiete } from "./pagese/wallet/WalletQuiet";
import { presentation } from "./pages/recharge/presentation";
import { Deposit } from "./pages/deposit/Deposit"
import { MyCode } from "./pages/myCode/MyCode"
import { Earnings } from "./pages/earnings/Earnings";
import { EarningsQuiet } from "./pages/earningsQuiet/EarningsQuiet";
import { Activate } from "./pages/activate/Activate";
import { activation } from "./pages/activation/activation";
import { Customer } from "./pages/customer/Customer";
import { WithdrawHistory } from "./pages/withdrawHistory/WithdrawHistory";
import { activationHistory } from "./pages/activationHistory/activationHistory";
import { DepositHistory } from "./pages/depositHistory/DepositHistory";
import { ChangeHistory } from "./pages/changeHistory/ChangeHistory";
import { MessageDetail } from "./pages/messageDetail/MessageDetail";
import { Registere } from './pagese/register/Register';
import { Logine } from './pagese/login/Login';
import { MyTeame } from './pagese/myTeam/MyTeam';
import { Settingse } from './pagese/settings/Settings';
import { MyCodee } from './pagese/myCode/MyCode';
import { Messagee } from './pagese/message/Message';
import { ForgetPwde } from './pagese/forgetPwd/ForgetPwd';
import { BankCarde } from './pagese/bankCard/BankCard';
import { Feedbacke } from './pagese/feedback/Feedback';
import { MessageDetaile } from './pagese/messageDetail/MessageDetail';
import { presentatione } from './pagese/recharge/presentation';
import { Earningse } from './pagese/earnings/Earnings';
import { IncomeRecorde } from './pagese/IncomeRecord/IncomeRecord';
import { UpdatePwde } from './pagese/updatePwd/UpdatePwd';
// let touch = UserStorage.getCookie('gesture');
// let touchPwd = UserStorage.getCookie("touchpwd");
const accessToken = UserStorage.getCookie("User.AccessTokenKey"),
      changeL = UserStorage.getCookie('changeL');
// const NotFound = () => (
//   <div> Sorry, this page does not exist. </div>
// )
class App extends React.Component {
  public render() {
    return (
      <Switch>
          <PrivateRoute exact path="/" component={Home} />
          <PrivateRoute exact path="/home" component={Home} />
          <PrivateRoute exact path="/homee" component={Homee} />
          <Route path="/login"  component={Login} />
          <Route path="/register"  component={Register} />
          <Route path="/logine"  component={Logine} />
          <Route path="/registere"  component={Registere} />
          <Route path="/idcard" component={IdCard} />
          <PrivateRoute path="/myTeam"  component={MyTeam} />
          <PrivateRoute path="/myTeame"  component={MyTeame} />
          <PrivateRoute path="/settings" component={Settings} />
          <PrivateRoute path="/settingse" component={Settingse} />
          <PrivateRoute path="/activation" component={activation} />
          <PrivateRoute path="/sales" component={Sales} />
          <PrivateRoute path="/myCode" component={MyCode} />
          <PrivateRoute path="/myCodee" component={MyCodee} />
          <PrivateRoute path="/message" component={Message} />
          <PrivateRoute path="/messagee" component={Messagee} />
          <PrivateRoute path="/community" component={Community} />
          <PrivateRoute path="/presentation" component={presentation} />
          <PrivateRoute path="/presentatione" component={presentatione} />
          <PrivateRoute path="/transactionPwd" component={TransactionPwd} />
          <PrivateRoute path="/transactionPwdForget" component={TransactionPwdForget} />
          <PrivateRoute path="/touchPwd" component={TouchPwd} />
          <Route path="/update_pwd" component={UpdatePwd} />
          <Route path="/update_pwde" component={UpdatePwde} />
          <Route path="/forget_pwd" component={ForgetPwd} />
          <Route path="/forget_pwde" component={ForgetPwde} />
          <PrivateRoute path="/bankCardAdd" component={BankCardAdd} />
          <PrivateRoute path="/bankCard" component={BankCard} />
          <PrivateRoute path="/bankCarde" component={BankCarde} />
          <PrivateRoute path="/feedback" component={Feedback} />
          <PrivateRoute path="/feedbacke" component={Feedbacke} />
          <PrivateRoute path="/incomeRecord" component={IncomeRecord} />
          <PrivateRoute path="/incomeRecorde" component={IncomeRecorde} />
          <PrivateRoute path="/exchangeAgreement" component={ExchangeAgreement} />
          <PrivateRoute path="/exchange" component={Exchange} />
          <PrivateRoute path="/change" component={Change} />
          <PrivateRoute path="/wallet" component={Wallet} />
          <PrivateRoute path="/walletQuiet" component={WalletQuiet} />
          <PrivateRoute path="/wallete" component={Wallete} />
          <PrivateRoute path="/walletQuiete" component={WalletQuiete} />
          <PrivateRoute path="/deposit" component={Deposit} />
          <PrivateRoute path="/earnings" component={Earnings} />
          <PrivateRoute path="/earningse" component={Earningse} />
          <PrivateRoute path="/earningsQuiet" component={EarningsQuiet} />
          <PrivateRoute path="/activate" component={Activate} />
          <PrivateRoute path="/customer" component={Customer} />
          <PrivateRoute path="/withdrawHistory" component={WithdrawHistory} />
          <PrivateRoute path="/activationHistory" component={activationHistory} />
          <PrivateRoute path="/depositHistory" component={DepositHistory} />
          <PrivateRoute path="/changeHistory" component={ChangeHistory} />
          <PrivateRoute path="/messageDetail" component={MessageDetail} />
          <PrivateRoute path="/messageDetaile" component={MessageDetaile} />
          <Route component={changeL=='0'?accessToken?Home:Login:accessToken?Homee:Logine} />
      </Switch>
    );
  }
}

export default App;



