import * as React from 'react';

import { NavBar, Icon, List,  } from "antd-mobile";
import { History } from "history";
import { UserService } from '../../service/UserService';
import icon from "../../assets/wallet_icon.png"
// import { UserStorage } from "../../storage/UserStorage";
// var changeL = UserStorage.getCookie('UserStorage');

interface WalletQuietProps {
    history: History
}

interface WalletQuietState {
    usable:string;
    // static:string;
    // profit:string;
    total:string;
    lock:string;
}
export class WalletQuiet extends React.Component<WalletQuietProps, WalletQuietState> {
   
    constructor(props: WalletQuietProps) {
        super(props);
        this.state = {
            usable:'0',
            // static:'0',
            // profit:'0',
            total:'0',
            lock:'0',
        }
    }

    onRedirectBack = () => {
        const history = this.props.history
        history.goBack()
    }
    onGoPage = () => {
        this.props.history.push("/incomeRecord")
    }
   
    public componentDidMount() {
        UserService.Instance.staticWallet('2').then((res:any) => {
            console.log(res.data)
            this.setState({
                usable:res.data.lastprofit,
                // static:res.data.static,
                // profit:res.data.profit,
                total:res.data.profit,
                lock:res.data.lockprofit,
            })
        })
       
    }
    public render() {
        return (
            <div className="withdraw-container">
                <NavBar mode="light" icon={<Icon type="left" key="0"/>}
                    onLeftClick={this.onRedirectBack}
                    rightContent={[
                        <div key="1" className="wallet-navbar-right" onClick={this.onGoPage}></div>
                    ]}
                    className="home-navbar" >
                    <div className="nav-title">收益钱包</div>
                </NavBar>
                <div className="withdraw-header-content">
                    <div className="fans-right-section">
                        <div className="fans-section-text">总收益</div>
                        <div className="fans-section-num">{this.state.total}</div>
                    </div>
                </div>
                <List>
                    {/* <List.Item thumb={icon} extra={this.state.static}>在投收益</List.Item> */}
                    <List.Item thumb={icon} extra={this.state.lock}>已提现收益</List.Item>
                    <List.Item thumb={icon} extra={this.state.usable}>可提现收益</List.Item>
                </List>
            </div>
        )
    }
}


