import * as React from 'react';

import { NavBar, Icon, List,  } from "antd-mobile";
import { History } from "history";
import { UserService } from '../../service/UserService';
import './Wallet.css'
import icon from "../../assets/wallet_icon.png"
// import { UserStorage } from "../../storage/UserStorage";
// var changeL = UserStorage.getCookie('UserStorage');

interface WalletProps {
    history: History
}

interface WalletState {
    usable:string;
    static:string;
    profit:string;
    total:string;
    lock:string;
}
export class Wallet extends React.Component<WalletProps, WalletState> {
   
    constructor(props: WalletProps) {
        super(props);
        this.state = {
            usable:'0',
            static:'0',
            profit:'0',
            total:'0',
            lock:'0',
        }
    }

    onRedirectBack = () => {
        const history = this.props.history
        history.goBack()
    }
    onGoPage = () => {
        this.props.history.push("/earnings")
    }
   
    public componentDidMount() {
        UserService.Instance.staticWallet('1').then((res:any) => {
            console.log(res.data)
            this.setState({
                usable:res.data.usable,
                static:res.data.static,
                profit:res.data.profit,
                total:res.data.total,
                lock:res.data.lock,
            })
        })
       
    }
    public render() {
        return (
            <div className="withdraw-container">
                <NavBar mode="light" icon={<Icon type="left" key="0"/>}
                    onLeftClick={this.onRedirectBack}
                    rightContent={[
                        <div key="1" className="wallet-navbar-right" onClick={this.onGoPage}></div>
                    ]}
                    className="home-navbar" >
                    <div className="nav-title">积分钱包</div>
                </NavBar>
                <div className="withdraw-header-content">
                    <div className="fans-right-section">
                        <div className="fans-section-text">总积分</div>
                        <div className="fans-section-num">{this.state.total}</div>
                    </div>
                </div>
                <List>
                    <List.Item thumb={icon} extra={this.state.static}>在投积分</List.Item>
                    <List.Item thumb={icon} extra={this.state.lock}>已提现积分</List.Item>
                    <List.Item thumb={icon} extra={this.state.usable}>可提现积分</List.Item>
                </List>
            </div>
        )
    }
}


