import * as React from 'react';
import ReactDOM from "react-dom";
import { NavBar, Icon, ListView } from "antd-mobile";
import { History } from "history";


import "./Earnings.css"

import { UserService } from '../../service/UserService';
import { model } from '../../model/model';

interface EarningsProps {
    history: History
}

interface EarningsState {
    dataSource: any,
    isLoading: boolean,
    hasMore: boolean,
    height: number,
    visible: boolean,
    lists: any[],
    selectedEarningStyle: string
}

export class Earningse extends React.Component<EarningsProps, EarningsState> {
    rData: any
    lv: any
    page:number
    key:string
    constructor(props: EarningsProps) {
        super(props);
        
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1: any, row2: any) => row1 !== row2,
          });
          this.page = 1
          this.state = {
            dataSource,
            lists: [],
            isLoading: true,
            hasMore: false,
            height:  document.documentElement.clientHeight - 200,
            visible: false,
            selectedEarningStyle: '1'
          };
        
    }

    onRedirectBack = () => {
        const history = this.props.history
        history.goBack()
    }

    componentDidMount() {
      this._loadDataWithStyle(this.state.selectedEarningStyle)
      
      }

      onEndReached = (event:any) => {
        // load new data
        // hasMore: from backend data, indicates whether it is the last page, here is false
        if (this.state.isLoading && !this.state.hasMore) {
          return;
        }   
        this.setState({ isLoading: true });
        this._loadDataWithStyle(this.key)

      }



    handleVisibleChange = (visible: boolean) => {
        this.setState({
            ...this.state,
            visible,
          });
    }

    onSelect = (opt: any) => {
        console.log("selected key", opt.key);
        this.key = opt.key;
        this.page = 1;
        this._loadDataWithStyle(opt.key)
        this.setState({
            ...this.state,
            visible: false,
            selectedEarningStyle: opt.key
        });
    }

    public render() {
        const separator = (sectionID: number, rowID: number) => (
            <div
              key={`${sectionID}-${rowID}`}
              style={{
                backgroundColor: '#F5F5F5',
                height: 1,
                // borderTop: '1px solid #ECECED',
                // borderBottom: '1px solid #ECECED',
              }}
            />
          );
       
          const row = (rowData: model.ProfitItem, sectionID: number, rowID: number) => {
            return (
              <div className="flex flex-jsb padding15 fs16">
                <div className="">
                  <div>{rowData.type == '1'?'Charge integral':rowData.type == '2'?'The principal withdrawal':rowData.type == '4'?'Income withdrawal' : 'earnings'}</div>
                  <div className="padding-t fs14 c666">{rowData.create_time}</div>
                </div>
                <div className="tar">
                  <div className={rowData.type != '2'||(rowData.type == '2' && rowData.status == '2')?"":"cfa4"}>
                  {rowData.type == '2'||rowData.type == '4'?rowData.status == '-1'?'invalid':rowData.status == '1'?'audit':rowData.status == '2'?'complete':'Trading closed':'normal'}
                  </div>
                  <div className={"padding-t " + (rowData.plusminus=="1"?"":"cfa4")}>{rowData.plusminus=="1"?'':'-'}{rowData.number}</div>
                </div>
              </div>
            );
          };

        return (
            <div className="message-container">
                <NavBar mode="light" icon={<Icon type="left" />} 
                    onLeftClick={ this.onRedirectBack}
                    className="home-navbar" 
                    >
                        <div className="nav-title">Integral record</div>
                </NavBar>
                <div className="fans-list-view-container">
                <ListView
                    ref={el => this.lv = el}
                    dataSource={this.state.dataSource}
                    renderFooter={() => (<div style={{ padding: 30, textAlign: 'center' }}>
                    {this.state.isLoading ? 'Loading...' : ''}
                    </div>)}
                    renderRow={row}
                    renderSeparator={separator}
                    className="am-list"
                    pageSize={4}
                    // useBodyScroll
                    onScroll={(e) => { console.log(e); }}
                    scrollRenderAheadDistance={500}
                    onEndReached={this.onEndReached}
                    onEndReachedThreshold={10}
                    style={{
                        height: this.state.height,
                        overflow: 'auto',
                    }}
                />
                </div>
            </div>
        )
    }

    private _loadDataWithStyle(selectedType?: string) {
      UserService.Instance.integralRecord(selectedType,this.page,).then( (profitData) => {
        const offsetTop = (ReactDOM.findDOMNode(this.lv)!.parentNode! as HTMLElement).offsetTop
        const hei = document.documentElement.clientHeight - offsetTop
        this.page = this.page + 1;
        let list:any = this.state.lists;
        profitData.list.map((res:any)=>{
            list.push(res)
        })
        this.setState({
          lists:list,
          dataSource: this.state.dataSource.cloneWithRows(list),
          isLoading: false,
          hasMore: false,
          height: hei
        });
      })
    }
   
}


