import * as React from 'react';
import { NavBar, Icon, List, InputItem, Button} from "antd-mobile";
import { History } from "history";
import { UserService } from '../../service/UserService';
import { UserStorage } from "../../storage/UserStorage";
import { UIUtil } from 'src/utils/UIUtil';

interface presentationProps {
    history: History
}


interface presentationState {
    agreement:boolean;
    setIndex:'1'|'2';
    bankVlaue: any;
    serviceNum: string;
    assets_min: string;
    assets_max: string;
    number: string;
    bank_name: string;
    name: string;
    bank_card_number: string;
    poundage: string;
}




export class presentation extends React.Component<presentationProps, presentationState> {
    poundage:string
    constructor(props: presentationProps) {
        super(props);
          this.state = {
            agreement:false,
            bankVlaue:[],
            setIndex:'1',
            serviceNum: '0',
            assets_min: '',
            assets_max: '',
            number: '',
            bank_name: '',
            name: '',
            bank_card_number: '',
            poundage: '',
        };
        
      }

    onRedirectBack = () => {
        const history = this.props.history
        history.goBack()
    }
    componentDidMount() {
        let bank:any = UserStorage.getCookie('bank')||'';
        bank = JSON.parse(bank)
        if(!bank.bank_card_number){
            UIUtil.showInfo("请添加银行卡");
            this.props.history.push({ pathname: "/bankCard", state: { messageType: "p" } });
            return;
        }
        UserService.Instance.getAssetsMaxMin().then( (res) => {
            this.poundage = res.data.poundage;
            this.setState({
                bank_name: bank.bank_name,
                name: bank.name,
                bank_card_number: bank.bank_card_number,
                assets_min:res.data.min,
                assets_max:res.data.max,
            })
        })

      }

    onNumberBlur = (val:string) => {
        this.setState({
            number:val
        })
    }
    onCash = (e:any) => {
        let id = e.currentTarget.dataset.id;
        this.setState({
            setIndex:id,
            agreement:true
        })
    }
    confirm = () => {
        let number = parseInt(this.state.number);
        let poundage = parseInt(this.poundage);
        let data = {
            type:this.state.setIndex,
            number: this.state.number,
            bank_name: this.state.bank_name,
            name: this.state.name,
            bank_card_number: this.state.bank_card_number,
            poundage: number * poundage / 100,
        }
        if(!this.state.number){
            UIUtil.showInfo("请输入金额");
        }
        if(this.state.number < this.state.assets_min){
            UIUtil.showInfo("最少可提现："+this.state.assets_min);
        }
        if(this.state.number > this.state.assets_max){
            UIUtil.showInfo("最多可提现："+this.state.assets_max);
        }
        UserService.Instance.assets(data).then( (res) => {
            UIUtil.showInfo("申请提现成功");
            this.props.history.push("/");
        }).catch( err => {
            UIUtil.showInfo("申请提现失败");
        })
    }
    public render() {
        const { agreement} = this.state
        if (!agreement) {
            return (
            <div>
                <NavBar mode="light" icon={<Icon type="left" />} 
                    onLeftClick={ this.onRedirectBack}
                    className="home-navbar" >
                        <div className="nav-title">选择提现</div>
                </NavBar>
                <div className="address-footer-button-container padding-txl"><Button data-id="1" onClick={this.onCash} >积分提现</Button></div>
                <div className="address-footer-button-container padding-txl"><Button data-id="2" onClick={this.onCash} >收益提现</Button></div>
            </div>
                )
        }
        return (
            <div className="fans-container">
                <NavBar mode="light" icon={<Icon type="left" />} 
                    onLeftClick={ this.onRedirectBack}
                    className="home-navbar" >
                        <div className="nav-title">提现</div>
                </NavBar>
                <List renderHeader={() => '提现数量'}>
                    <InputItem placeholder={"最少可提现："+this.state.assets_min+"，最多可提现："+this.state.assets_max} type="digit" onBlur={this.onNumberBlur} ></InputItem>
                </List>
                <List renderHeader={() => '银行卡'}>
                <List.Item arrow="horizontal"
                        onClick={() => {
                            this.props.history.push("/bankCard")
                        }}>
                        {this.state.name}
                        <List.Item.Brief>{this.state.bank_name}    银行卡号：{this.state.bank_card_number}</List.Item.Brief>
                </List.Item>
                </List>
                <List renderHeader={() => '手续费'+this.state.serviceNum}></List>
                <div className="address-footer-button-container padding-txl"><Button onClick={this.confirm} >申请</Button></div>
            </div>
        )
    }
}


