export class Homee extends React.Component<HomeProps, HomeState> {
    rData: any
    lv: any
    avatarInput: any
    inapp?: string
    min:number
    max:number
    interva: any
    constructor(props: HomeProps) {
        super(props)
        this.state = {
            selectedTab: "HomeTab",
            showQRCode: false,
            isSignIn: false,
            remove:false,
            list:[],
            index:-1,
            xstr:null,
            ystr:null,
            imgHeight:'1',
            coinlist: [],
            CoinData: [],
            sValue: [],
            klinesList:[],
            vipName:'县级'
        }
    }
    // onTapIntegral =() =>{
    //     this.props.history.push("/integralRecord")
    // }
    onTapMyMenu = (el: object, index: number) => {
        if (index == 0) {
            this.props.history.push("/wallete")
        } else if (index == 1) {
            this.props.history.push("/walletQuiete")
        } else if (index == 2) {
            this.props.history.push("/myTeame")
        } else if (index == 3) {
            this.props.history.push("/bankCarde")
        } else if (index == 4) {
            this.props.history.push("/myCodee")
        }else if (index == 5) {
            this.props.history.push({ pathname: "/messagee", state: { messageType: "2" } })
        }else if (index == 6) {
            this.props.history.push({ pathname: "/messagee", state: { messageType: "1" } })
        }else if (index == 7) {
            this.props.history.push("/settingse")
        }else if (index == 8) {
            this.props.history.push("/feedbacke")
        }
    }
    onCloseQRCode = () => {
        this.setState({
            ...this.state,
            showQRCode: false
        })
    }
    onAvatarChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        event.preventDefault()
        const files = event.target.files
        if (!files || files.length == 0) {
            return 
        }
        UIUtil.showLoading("upLoad...")
        UserService.Instance.uploadFile(files[0]).then( avatarUrl => {
            const userInfo = this.state.userInfo
            if (userInfo) {
                userInfo.head_imgurl = avatarUrl
                this.setState({
                    userInfo: userInfo
                })
            }
            UIUtil.hideLoading()
        }).catch( err => {
            UIUtil.showError(err)
        })
    }

    getExchangeRate = (ticker:string) => {
        // UserService.Instance.tick(ticker).then( datalist => {
        //     let list = this.state.klinesList;
        //     let max = Math.max(...list)+"";
        //     let min = Math.min(...list)+"";
        //     UserStorage.setCookie('exchange_rate',datalist.low)
        //     this.max = Math.ceil(parseFloat(max));
        //     this.min = Math.floor(parseFloat(min));
        //     console.log("high:"+datalist.high)
        //     console.log("close:"+datalist.close)
        //     console.log("low:"+datalist.low)
        //     list[4] = datalist.low;
        //     this.setState({
        //         klinesList:list
        //     })
        // })
    }
    public componentDidUpdate() {

    }
    public componentWillMount() {
        
    }
    public componentDidMount() {
        const query = qs.parse(this.props.location.search, {
            ignoreQueryPrefix: true
        })
        if (query.change) {
            this.props.history.push("/register",{mobile:query.mobile})
            return;
        }
        const userAgentItemList = window.navigator.userAgent.split(" ")
        for (const item of userAgentItemList) {
            if (item.startsWith("inapp/")) {
                this.inapp = item
                break
            }
        }
        UserService.Instance.getUserInfo().then( userInfo => {
            if (userInfo.errmsg) {
                this.props.history.push("/login");
                return;
            }else{
                UserStorage.setCookie('mobile',userInfo.mobile)
                this.setState({
                    ...this.state,
                    userInfo: userInfo
                })
            }
        }).catch ( err => {
            
        })
        UserService.Instance.pageIndex().then( pageIndexData => {
            this.setState({
                pageIndexData: pageIndexData
            })
        })
    }
    public render() {
        return (
            <div className="home-container">
                    <div className="tab-bar-container margin-t0">
                                <div className="profile-header">

                                    <div className="h8 padding4 flex flex-ac flex-jsb">
                                    <div className="flex">
                                        <div className="avatar-container">
                                            <input className="avatar-input" type="file"  ref={el => this.avatarInput = el} onChange={this.onAvatarChange}/>
                                            <img className="profile-header-logo" src={this.state.userInfo && this.state.userInfo.head_imgurl }/>
                                        </div>
                                        
                                        <div className="profile-header-info-container">
                                            <div className="profile-header-nickname">{this.state.userInfo && this.state.userInfo.nickname}</div>
                                            <div className="profile-header-phone">{this.state.userInfo && this.state.userInfo.mobile}</div>
                                        </div>
                                    </div>
                                    </div>
                                    <div className="w100 flex flex-js flex-ac tac cfff">
                                        <div className="home-top-item fs-34" onClick={()=>{this.props.history.push("/wallet")}}> 
                                            <div className="home-top-title">With integral</div><div className="home-top-num">{this.state.pageIndexData && this.state.pageIndexData.static}</div>
                                        </div>
                                        <div className="home-top-item fs-34" onClick={()=>{this.props.history.push("/presentation")}}> 
                                            <div className="home-top-title">Can carry integral</div><div className="home-top-num">{this.state.pageIndexData && this.state.pageIndexData.usable}</div>
                                        </div>
                                        <div className="home-top-item fs-34" onClick={()=>{this.props.history.push("/walletQuiet")}}> 
                                            <div className="home-top-title">Earnings yesterday</div><div className="home-top-num">{this.state.pageIndexData && this.state.pageIndexData.profit}</div>
                                        </div>
                                    </div>
                                    
                                </div>
                                <List className="my-profile-list">
                                    <Grid data={myBottomMenuData} hasLine={false} onClick={this.onTapMyMenu} columnNum={3}></Grid>
                                </List>
                                </div>
                    </div>
        )
    }
}


